import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';

import {Subject} from 'rxjs/Rx';

import { Recipe } from '../recipe';

import { ShoppingListService } from '../../shopping-list/shopping-list.service';

@Component({
  selector: 'rb-recipe-item',
  template: require('./recipe-item.component.html')
})
export class RecipeItemComponent implements OnInit {

  @Input() recipe: Recipe;
  @Input() recipeId: number;

  links;

  constructor(private sls: ShoppingListService, private router: Router) {
    this.links = [
      {title:'Add ingredients to Shopping List', isEnabled: true, subject:new Subject()},
      {title:'Edit this recipe', isEnabled: true, subject:new Subject()},
      {title:'Debug - Recipe details to console.log', isEnabled: true, subject:new Subject()}
    ];
  }

  ngOnInit() {
    this.links[0].subject.subscribe(val => this.opcio1(val));
    this.links[0].isEnabled = this.recipe.hasOwnProperty('ingredients') && this.recipe.ingredients.length > 0;
    this.links[1].subject.subscribe(val => this.opcio2(val));
    this.links[2].subject.subscribe(val => this.opcio3());
  }

  opcio1(link){
    this.sls.addItems(this.recipe.ingredients);
    this.router.navigate(['/shopping-list']);
  }

  opcio2(link){
    this.router.navigate(['/recipes', this.recipeId, 'edit']);
  }

  opcio3(){
    console.log(this.recipe);
  }

}



